.wl-postCard {
    padding-right: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.2s;
}
.wl-postCard:hover {
    -ms-transform: scale(1.04); /* IE 9 */
    -webkit-transform: scale(1.04); /* Safari 3-8 */
    transform: scale(1.04);
}
